@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-image: linear-gradient(
    to right bottom,
    #051937,
    #004166,
    #006d76,
    #00955d,
    #88b52c
  );
}

.App {
  text-align: center;
}

h1 {
  font-family: 'Roboto Mono', monospace;
  font-size: 70px;
  margin: 20px;
  color: rgb(158, 204, 158);
}

.Card_Card__1l0YT {
  margin: 1rem 1rem;
  background-color: lightgreen;
  border: 2px solid lightgreen;
  border-radius: 15px;
  padding: 0.5rem;
  transition: all 0.2s ease-in-out;
}

.Card_Card__1l0YT:hover {
  cursor: pointer;
  transform: scale(1.03);
  border-width: 0px;
  -o-box-shadow: rgba(0, 0, 0, 0.8) 0px 5px 15px,
    inset rgba(0, 0, 0, 0.15) 0px -10px 20px;
  box-shadow: rgba(0, 0, 0, 0.8) 0px 5px 15px,
    inset rgba(0, 0, 0, 0.15) 0px -10px 20px;
}

.Cards_Container__1Ooub {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(330px, 1fr));
  grid-template-rows: minmax(200px, 500px);
  grid-gap: 0.1rem;
}

.Searchbox_search__IANQD {
  height: 100%;
  width: 100%;
  display: flex;
  position: relative;
}

.Searchbox_searchTerm__1s0iY {
  width: 100%;
  border: 3px solid #00b4cc;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #4c3838;
}

.Searchbox_searchTerm__1s0iY:focus {
  color: #426063;
}

.Searchbox_searchButton__3js6V {
  width: 40px;
  height: 36px;
  border: 1px solid #00b4cc;
  background: #00b4cc;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.Searchbox_wrap__1TJt9 {
  height: 1rem;
  width: 12rem;
  position: relative;
  top: 34%;
  left: 50%;
  transform: translate(-65%, -20%);
  margin: 50px;
}

.Searchbox_SearchIcon__165DO {
  width: 25px;
  height: 25px;
}

.Scrollbar_Scroll__3k8Fd {
  overflow-y: scroll;
  border: 1px solid black;
  height: 500px;
}

